.formContainer {
    display: flex;
    gap: 20px;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 10px;
    padding: 10px;
}

.lookingBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.lookingRadio {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contacts {
    display: flex;
    flex-direction: column;
}

.contacts p {
    margin: 6px;
}

.formContainer button {
    align-self: end;
    font-size: 14px;
}

.formContainer input {
    background: transparent;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid rgb(143, 143, 143);
}

.formContainer input:focus,
.formContainer textarea:focus {
    outline: 1px solid rgb(178, 255, 247);
}

.formContainer textarea {
    outline: none;
    border: none;
    border-radius: 5px;
    resize: none;
    width: 170px;
    background: transparent;
    border: 1px solid rgb(143, 143, 143);
    padding: 5px;
}

.formContainer textarea::-webkit-scrollbar {
    width: 10px;
}

.formContainer textarea::-webkit-scrollbar-track {
    background: transparent;
    border-radius: 10px;
}

.formContainer textarea::-webkit-scrollbar-thumb {
    background: rgba(255, 205, 169, 0.799);
    border-radius: 10px;
}

.formContainer i {
    color: red;
    font-size: 12px;
}

.lookingBlock input[type="radio"]:focus {
    outline: none;
}