.avatarContainer {
    display: flex;
    flex-direction: column;
    width: 130px;
}

.avatarContainer img {
    width: 130px;
    height: 130px;
    object-fit: cover;
}

.avatarContainer input {
    width: 128px;
    padding: 5px;
}

.avatarContainer input::file-selector-button {
    border: none;
    background: rgba(255, 205, 130, 0.633);
    border-radius: 10px;
    padding: 5px 10px;
    color: rgb(79, 79, 79);
    transition: box-shadow .2s ease;
}

.avatarContainer input::file-selector-button:hover {
    color: rgb(59, 59, 59);
    box-shadow: inset 0px 0px 8px 0px rgba(0,0,0,0.1);
}

.avatarContainer i {
    color: red;
    text-align: center;
    font-size: 12px;
    word-break: normal;
}