.container {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto 1fr 1fr;
    column-gap: 16px;
    border-bottom: 1px solid rgb(241, 241, 241);
    border-top: 1px solid rgb(241, 241, 241);
}

.container div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container i {
    line-height: 1;
}

.container p {
    line-height: 1;
}

.looking,
.aboutMe {
    max-width: 180px;
}

.aboutMe {
    display: flex;
    flex-direction: column;
   justify-content: center;
}

.container button {
    width: 100px;
    height: 30px;
    font-size: 12px;
    background: rgb(255, 255, 170); 
}